.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: var(--z-top);
}

.inner {
    position: relative;
    padding: var(--app-padding);
    width: 100%;
    max-height: 60vh;
    overflow-y: auto;
    background-color: rgb(var(--color-container));
    border-radius: 10px;
    scrollbar-width: thin;
    scrollbar-color: rgba(var(--color-container-selected), 0.5) transparent;
}

.inner::-webkit-scrollbar {
    width: 2vh;
    background-color: rgb(var(--color-container));
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}

.inner::-webkit-scrollbar-thumb {
    background-color: rgba(var(--color-container-selected), 0.5);
    border: 0.5vh solid transparent;
    border-radius: 10px;
    background-clip: padding-box;
}

.inner::-webkit-scrollbar-thumb:hover {
    background-color: rgb(var(--color-container-selected));
}

.inner::-webkit-scrollbar-track {
    background-color: transparent;
}

.close {
    display: flex;
    justify-content: flex-end;
}